<template>
	<view class="Model">
		<modellist v-for='(item,index) in classifyData' :key='index' :items='item' :MyModel='MyModel'></modellist>
	</view>
</template>

<script>
	import {getClassifyModel} from '@/utill/api/model/getModel.js'
	import modellist from '@/components/model/modellist.vue'
	export default{
		components:{
			modellist
		},
		data(){
			return{
				classifyData:[],
				MyModel:1,//用于区分我的模型库和公开模型库进入详情页的不同
			}
		},
		methods:{
			getClassifyModel(id){
				getClassifyModel({
					pageNum:1,
					pageSize:100,
					modelCategory:id,
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.classifyData=res.data.object.list
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		},
		onLoad(options) {
			console.log(options.modelLibTypeName)
			uni.setNavigationBarTitle({
				title:options.modelLibTypeName
			})	
			console.log(options.classifyId)
			this.getClassifyModel(options.classifyId)
		}
	}
</script>

<style scoped lang="scss">
	.Model{
		padding-bottom: 30rpx;
	}
</style>
